import axios from 'common/utils/axios'
import axiosa from 'axios'
var objectStore = ''

export default function get_car_librarys_csv(callback) {
  // 缓存名
  var cacleName = 'car_librarys_csv_874654166'

  // 如果 window 缓存了数据 直接返回
  if (window[cacleName]) return callback({ code: 200, data: window[cacleName] })


  // 获取数据库对象
  function getDB(cb) {
    // 如果 存在兼容问题 返回 false
    if (!(window.localStorage && window.indexedDB && window.indexedDB.open)) return cb(false)

    // 获取 数据库
    var request = indexedDB.open("jybd-car", 1)
    // 监听错误
    request.onerror = function (event) {
      console.log("数据库打开报错")
      cb(false)
    }
    // 监听成功
    request.onsuccess = function (event) {
      console.log("数据库打开成功")
      // 返回数据库 对象
      cb(request.result)
    }
    // 监听数据库变化
    request.onupgradeneeded = function (event) {
      // 如果数据库已存在
      if (event.target.result.objectStoreNames.contains("car_choice")) return
      // 创建person仓库(表) 主键
      objectStore = event.target.result.createObjectStore("car_choice", {
        keyPath: "id",
      })
    }
  }
  // 获取远端数据
  function getCSV(url, cb) {
    axiosa.get(url).then((str) => {
          // 数据拆分  行
          var csvarry = str.data.split("\n")
          // 数据缓存
          var datas = []
          // 获取 字段名列表
          var headers = csvarry[1].split(",")
          // 循环获取数据
          for (var i = 2; i < csvarry.length; i++) {
            // 当前数据缓存
            var data = {}
            // 获取 字段值 列
            var temp = csvarry[i].split(",")
            // 循环列
            for (var j = 0; j < temp.length; j++) {
              // 如果 字段名存在  赋值
              if (headers[j]) data[headers[j]] = temp[j]
            }
            // 如果 当前数据 存在car_lib_id 添加到数据缓存数组
            if (data.car_lib_id) datas.push(data)
          }
          // 返回数据
          cb({ code: 200, data: datas })
        }
        )
  }
  // 设置缓存
  function setData(data, csv_res) {
    // 更新数据结构

    // 在window 设置缓存
    window[cacleName] = data

    // 获取 数据库 对象
    getDB(function (db) {
      // 如果 获取不了 直接返回
      if (db === false) return

      // 存储数据
      var request = db
        .transaction(["car_choice"], "readwrite")
        .objectStore("car_choice")
        .put({ id: 0, data: data })

      // 监听成功
      request.onsuccess = function (event) {
        // 设置存储成功 标记
        window.localStorage.setItem(cacleName, csv_res.data.update_time)
        console.log('数据库存储成功');
      }

      // 监听失败
      request.onerror = function (event) {
        console.log("Database error: ", event.target.error)
      }
    })
  }
  // 获取缓存数据
  function getData(csv_res, cb) {
    // 如果 缓存标记 与 接口返回不一致
    if (csv_res.code == 200 && window.localStorage && window.localStorage.getItem && window.localStorage.getItem(cacleName) != csv_res.data.update_time) {
      return cb()
    }

    // 获取 数据库 对象
    getDB(function (db) {
      // 如果 获取不了 直接返回
      if (db === false) return cb()

      // 查询表
      var request = db.transaction(["car_choice"]).objectStore("car_choice").get(0)

      // 监听查询成功
      request.onsuccess = function (event) {
        console.log('数据库读取成功');
        // 获取存储数据
        var data = request.result && request.result.data
        // 返回存储的数据
        cb(data)
        // 如果存储数据存在  缓存在window
        if (data) window[cacleName] = data
      }
      // 监听查询失败
      request.onerror = function (event) {
        console.log("Database error: ", event.target.error)
        cb()
      }
    })
  }
  // 获取接口 更新信息
  function getAjax(cb) {
    axios({
      type: 'get',
      url: 'v2/open/Excel/getCarLibraryExcel'
    }).then((res) => {
      try {  res = JSON.parse(res.data.data) } catch (error) {}
      cb({ code: res.code, data: res.result })
    }).catch(() => {
      cb({ code: 400, message: '服务器错误，请联系管理员' })
    })

  }

  // 更新数据结构(按字母分类)
  function updateDataStructure(data) {
    const result = {}
    data.forEach(item => {
      if (!result[item.brand_letter]) {
        result[item.brand_letter] = []
      }
      result[item.brand_letter].push(item)
    });
    return result
  }

  // 请求接口获取 文件信息
  getAjax(function (csv_res) {
    // 如果数据存在 使用缓存数据
    getData(csv_res, function (res) {
      // 如果数据存在  返回数据
      if (res) return callback({ code: 200, data: res })

      // 如果返回错误
      if (csv_res.code != 200) return callback(csv_res)

      // 获取 远端数据
      getCSV(csv_res.data.url, function (res) {
        // 返回数据
        res.data = updateDataStructure(res.data)
        callback(res)
        // 如果数据正确
        if (res.code == 200) {
          // 延迟 存储数据
          setTimeout(function () { setData(res.data, csv_res) }, 100);
        }
      })
    })
  })
}


