<template>
  <div class="params">
    <vxe-modal v-model="value" title="适配车型" @hide="$emit('input', false)" id="inReceiptSelectOrder" width="1200" :esc-closable="true" destroy-on-close height="500" min-width="600" min-height="100" resize remember storage transfer>
      <div v-loading="loading">
        <div v-if="tableData.length">
          <div class="el_tree_box">
            <xTable ref="xTable" :tableColumn="tableColumn" maxHeight="380" :xData="tableData" />
            <div class="block">
              <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[10, 20, 30, 50]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="total">
              </el-pagination>
            </div>
          </div>
        </div>
        <div class="dialog-footer" v-else-if="!loading">
          适配车型无数据
        </div>
      </div>
    </vxe-modal>
  </div>
</template>

<script>
import get_car_librarys_csv from "./config/carModel.js";
export default {
  data() {
    return {
      loading: false,
      tableColumn: [
        {
          title: "车款",
          field: "sales_name",
          formatter: ({ row }) => {
            return row.sales_name ? row.sales_name : row.attr_value_name;
          },
          width: 250,
        },
        {
          title: "品牌",
          field: "brand_name",
          width: 70,
        },
        {
          title: "厂家",
          field: "show_name",
          width: 100,
        },
        {
          title: "车系",
          field: "car_series",
          width: 120,
        },
        {
          title: "排放标准",
          field: "emission_standard",
          width: 70,
        },
        {
          title: "年款",
          field: "years",
          width: 100,
        },
        {
          title: "上市年份",
          field: "c_variant_sop",
          width: 120,
        },
        {
          title: "生产年份",
          field: "c_variant_sop",
          width: 120,
        },
        {
          title: "停产年份",
          field: "c_variant_eop",
          width: 100,
        },
        {
          title: "排量",
          field: "displacement",
          width: 100,
        },
        {
          title: "发动机",
          field: "engine_type",
          width: 100,
        },
        {
          title: "适配来源",
          field: "unit_name",
          width: 100,
          formatter: ({ row }) => {
            return this.idsObj[row.car_lib_id] == 1 ? "OE码匹配" : "自主选择";
          },
        },
      ],
      idsObj: {},
      tableData: [],
      currentPage: 1,
      pageSize: 10,
      total: 0,
    };
  },
  watch: {
    value(newVal) {
      if (newVal === true) {
        if (this.type === 2) {
          this.getLibraryId();
        }
      }
    },
    library_id() {
      if (this.type === 1) {
        this.getLibraryId();
      }
    },
    ids() {
      this.getData();
    },
  },
  props: {
    value: null,
    type: {
      // type == 1 详情  2是列表
      type: Number,
      default: 1,
    },
    library_id: {
      type: Number | String,
      default: "",
    },
    shop_goods_id: {
      type: Number | String,
      default: "",
    },
    ids: {
      type: Array,
      default: () => [],
    },
  },
  created() {
    this.alltableData = [];
    this.allData = [];
    get_car_librarys_csv((res) => {
      var result = [];
      for (var i in res.data) {
        for (var j of res.data[i]) {
          result.push(j);
        }
      }
      this.allData = result;
      this.$emit("loaded");
      if (this.type == 1) {
        this.getData();
      }
    });
  },
  methods: {
    getLibraryId() {
      this.init();
      if (this.library_id) {
        this.loading = true;
        this.$api
          .serviceStationGetOeCodeOrCarLibraryList({
            library_id: this.library_id,
          })
          .then(({ data }) => {
            if (data.code == 200) {
              var box = [];
              var oe = [];
              for (var i in data.data.car_library) {
                box = box.concat(data.data.car_library[i]);
              }
              for (var j in data.data.oe_code) {
                oe.push(data.data.oe_code[j]);
              }
              this.ids = box;
              this.$emit("getOeCode", oe.join(","));
            }
            this.loading = false;
            this.getAttributeCarLibraryList();
          })
          .catch(() => {
            this.loading = false;
          });
      } else {
        this.getAttributeCarLibraryList();
      }
    },
    submit() {
      this.$emit("sumbitData", {
        params: this.currentRow,
        selectBox: this.selectBox,
      });
    },
    GetValue(library_id) {
      if (library_id) {
        return this.$api.serviceStationGetOeCodeOrCarLibraryList({
          library_id,
        });
      }
    },
    getAttributeCarLibraryList() {
      if (this.shop_goods_id && this.ids.length === 0) {
        this.$api
          .getAttributeCarLibraryList({
            shop_goods_id: this.shop_goods_id,
            // shop_goods_id: 506298153,
          })
          .then(({ data }) => {
            if (data.code == 200) {
              var twoBox = [];
              for (var i in data.data) {
                twoBox = twoBox.concat(data.data[i]);
              }
              this.alltableData = twoBox;
              this.handleCurrentChange(1);
            }
          });
      }
    },
    getFairModelData(arr) {
      var html = "";
      for (var i = 0; i < arr.length; i++) {
        if (html.length > 100) {
          break;
        }
        html = html + arr[i].sales_name + ",";
      }
      this.$emit("renderModel", html.slice(0, -1));
      this.$parent.showModelsName = html.slice(0, -1);
    },

    handleCurrentChange(index) {
      var a = this.alltableData.concat([]);
      this.tableData = a.splice((index - 1) * this.pageSize, this.pageSize);
    },

    handleSizeChange(pageSize) {
      this.pageSize = pageSize;
    },
    init() {
      this.currentPage = 1;
      this.total = 0;
      this.alltableData = [];
      this.tableData = [];
    },
    getData() {
      this.init();
      if (this.allData.length && this.ids.length) {
        this.idsObj = {};
        for (var i = 0; i < this.ids.length; i++) {
          if (this.ids[i].car_lib_id == -1) {
            this.total = this.allData.length;
            this.alltableData = this.allData;
            this.handleCurrentChange(1);
            this.$emit("getData", [{ sales_name: "通用车型" }]);
            this.getFairModelData([{ sales_name: "通用车型" }]);
            return;
          }
          this.idsObj[this.ids[i].car_lib_id] = this.ids[i].match_type;
        }
        var selfArr = [];
        var result = [];
        this.allData.forEach((val) => {
          if (this.idsObj[val.car_lib_id] == 1) {
            result.push(val);
          }
          if (this.idsObj[val.car_lib_id] == 0) {
            selfArr.push(val);
          }
        });
        this.total = result.length + selfArr.length;
        this.alltableData = selfArr.concat(result);
        this.handleCurrentChange(1);
        this.$emit("getData", this.alltableData);
        this.getFairModelData(this.alltableData);
      }
    },
  },
};
</script>

<style scoped lang="less"></style>
