<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>库存管理</el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goBack">公司商品信息</span>
      </el-breadcrumb-item>
      <el-breadcrumb-item>查看公司商品信息</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="companyGoodsInfoSee">
      <el-form :model="params" class="demo-form-inline" label-width='150px'>
        <el-form-item label="商品名称" size="mini">
          <el-input readonly v-model="params.goods_name"></el-input>
        </el-form-item>
        <el-form-item label="商品分类" size="mini">
          <el-input readonly v-model="params.goods_type_name"></el-input>
        </el-form-item>
        <el-form-item label="商品品牌" size="mini">
          <el-input readonly v-model="params.brand_name"></el-input>
        </el-form-item>
        <el-form-item label="商品名称" size="mini">
          <el-input readonly v-model="params.goods_name"></el-input>
        </el-form-item>
        <el-form-item label="商品单位" size="mini">
          <el-input readonly v-model="params.unit_name"></el-input>
        </el-form-item>
        <el-form-item :label="modelLabel" size="mini">
          <el-input readonly v-model="params.goods_model"></el-input>
        </el-form-item>
        <el-form-item label="材质" size="mini" v-if="params.goods_texture">
          <el-input readonly v-model="params.goods_texture"></el-input>
        </el-form-item>
        <el-form-item label="商品编码" size="mini">
          <el-input readonly v-model="params.goods_id"></el-input>
        </el-form-item>
        <el-form-item label="销售价" size="mini">
          <el-input readonly v-model="params.guide_price"><template slot="append">元</template></el-input>
        </el-form-item>
        <el-form-item label="自定义编码" size="mini">
          <el-input readonly v-model="params.goods_sn"></el-input>
        </el-form-item>
        <el-form-item label="工厂编码" size="mini">
          <el-input readonly v-model="params.factory_code"></el-input>
        </el-form-item>
        <el-form-item label="销售税率" size="mini">
          <el-input readonly v-model="params.taxrate"><template slot="append">%</template></el-input>
        </el-form-item>
        <div>
          <el-form-item label="适配车型" size="mini" style="width: 100%;">
            <span>{{ showModelsName }}</span>
            <el-button type='primary' style="width: auto;" @click="isShowFairModel = true">查看全部</el-button>
          </el-form-item>
        </div>
        <div>
          <el-form-item label="车型匹配码" size="mini">
            <el-input readonly v-model="params.oe_code" type='textarea' :rows='5' :cols='20'></el-input>
          </el-form-item>
        </div>
        <el-form-item label="商品图片" size="mini" style="width: 100%;">
          <cupload v-model="params.image" :beforeRemove='() => false' v-if='params.image'>
          </cupload>
        </el-form-item>
        <div>
          <el-form-item label="备注" size="mini">
            <el-input readonly v-model="params.remark" type='textarea' :rows='5' :cols='20'></el-input>
          </el-form-item>
        </div>
      </el-form>
    </div>
    <div class="dialog-footer">
      <el-button @click="goBack">返 回</el-button>
    </div>

    <modelDetail v-model='isShowFairModel' @getOeCode='(val) => { params.oe_code = val }' ref='modelDetail' :library_id='params.library_id' @renderModel='(val) => showModelsName = val' />
  </div>
</template>

<script>
import modelDetail from "common/components/plugin/service/modelDetail.vue";
export default {
  name: "service_station_inventory_goods_see",
  data() {
    return {
      params: {},
      showModelsName: "",
      modelLabel: "",
      isShowFairModel: false,
    };
  },
  methods: {
    goBack() {
      this.$router.push({
        path: "/service_station_goods/service_station_inventory_goods",
      });
    },
  },
  components: {
    modelDetail,
  },
  created() {
    this.$api
      .serviceStationgetGoodsInfo({ inventory_goods_id: this.$route.query.id })
      .then(({ data }) => {
        if (data.code == 200) {
          this.params = data.data;
          this.$api
            .serviceStationGetTypeParameterList({
              goods_type_id: data.data.goods_type_id,
            })
            .then(({ data }) => {
              if (data.code == 200) {
                this.modelLabel = Object.keys(data.data).filter(
                  (val) => val != "材质"
                )[0];
              }
            });
        }
      });
  },
};
</script>

<style scoped lang='less'>
.companyGoodsInfoSee {
  /deep/ .el-form-item {
    width: 100%;
    .el-textarea {
      width: 500px;
    }
  }
  .information {
    color: rgba(0, 0, 0, 0.5);
    font-size: 12px;
  }
}
</style>
